'use strict';

/**
* Erlang distributed pseudorandom numbers.
*
* @module @stdlib/random-base-erlang
*
* @example
* import erlang from '@stdlib/random-base-erlang' ;
*
* var v = erlang( 3, 2.5 );
* // returns <number>
*
* @example
* import { factory as factory } from '@stdlib/random-base-erlang' ;
*
* var erlang = factory( 8, 5.9, {
*     'seed': 297
* });
*
* var v = erlang();
* // returns <number>
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import erlang from './main.js' ;
import factory from './factory.js' ;


// MAIN //

setReadOnly( erlang, 'factory', factory );


// EXPORTS //

export default erlang;
